@keyframes fade-in {
    from {
      /* you cant animate display property */
      visibility: hidden;
      opacity: 0;
    }
    1% {
      visibility: visible;
      opacity: 0;
    }
    to {
      opacity: 1;
      visibility: visible;
    }
  }
  
  /* Helper class */
  .fade-in {
    animation: fade-in 200ms ease-in-out forwards; //forwards stays at whatever the final result is
  }
  
  @keyframes fade-out {
    from {
      /* you cant animate display property */
      visibility: visible;
      opacity: 1;
    }
  
    99%{
        visibility: visible;
        opacity: 0;
    }
    to {
        visibility: hidden;
      opacity: 0;
    }
  }
  
  .fade-out {
    animation: fade-out 200ms ease-in-out forwards; //forwards stays at whatever the final result is
  }